@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
#quote-box {
  background-color: var(--secondary-color);
  width: clamp(400px, 50vw, 600px);
  height: clamp(200px, 50vw, 300px);
  align-items: center;
  border-radius: 7px;
  padding: 40px;
}

#text {
  min-height: 60px;
  width: 100%;
  flex: 2 1;
}

#author {
  font-size: 17px;
  width: 100%;
  text-align: right;
  flex: 1 1;
}

#buttons-container {
  width: 100%;
  height: 50px;
  justify-content: space-between;
  align-items: center;
}

#new-quote {
  border: none;
  outline: none;
  background-color: var(--text-color);
  padding: 15px 30px;
  border-radius: 7px;
  color: var(--text-color-light);
  font-weight: bold;
  cursor: pointer;
  transition: 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
}

#new-quote:hover {
  background-color: var(--accent-color);
}

.quote-icon {
  font-size: 35px;
  color: var(--text-color);
  transition: 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.quote-icon:hover {
  color: var(--accent-color);
}

:root {
  --primary-color: #ab4e68;
  --secondary-color: #eef1ef;
  --accent-color: #f0b7b3;
  --text-color: #533745;
  --text-color-light: #eef1ef;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #ab4e68;
  background-color: var(--primary-color);
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans", sans-serif;
  color: #533745;
  color: var(--text-color);
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #533745;
  color: var(--text-color);
}

.App {
  font-size: 25px;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.centered {
  justify-content: center;
  align-items: center;
}

main {
  width: 100%;
  min-height: 100vh;
}

